html, body, #root {
  height: 100%;
  overflow: hidden;
}

.body {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  font-family: 'Open Sans';
}

blockquote {
  margin-left: 2rem !important;
  font-style: italic;
}

.pg-page {
  background-color: #FB5447;
}

.page-bg-image {
  position: relative;
  width: 100%;
  height: 100%;
  background-size: 100%;
  /* background-position-y: 55%; */
  background-repeat: no-repeat;
  overflow: hidden;
}

.page { 
  position: relative;
  width: 100%;
  height: 100%;
}

.photo {
  position: absolute;
  top: 15px;
  width: 90%;
  left: 5%;
  height: 40%;
  animation: photoanimation 10s infinite;
  background-size: 100%;
  background-position-x: center;
  background-repeat: no-repeat;
}

@keyframes photoanimation {
  0% { background-size: 100%; }
  /* 25% { background-size: 110%; } */
  50% { background-size: 110%; }
  /* 75% { } */
  100% { background-size: 100%; }
}

.page3-content {
  position: relative;
  width: 100%;
  top: 92vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.button-group {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.button {
  width: 26vw;
  height: 15.5vw;
  background-size: cover;
  margin: 1vw;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 10px 7px 10px;
  font-weight: bold;
  text-align: center;
  color: white;
}

.button.button-active {
  color: #DD241B;
  padding: 7px 10px 0 10px;
}

.input {
  width: 81vw;
  margin-top: 2.5vw;
  border-radius: 0;
  height: 13vw;
  border: 2.5px solid #FF9743;
  background: #FFF0D4;
  padding: 0 14px;
}
.input::placeholder {
  color: #CC786D;
}